body {
  font-family: "Arial", sans-serif;
  text-align: center;
  margin: 0;
  padding: 0;
  --djH: 30;
  background: hsl(var(--djH), 30%, 90%);
}

.color1 {
  --djH: 0;
}

.container {
  max-width: 500px;
  margin: 0 auto;
  padding: 20px;
}

.image-section {
  position: relative;
  text-align: center;
}

.image-section img {
  width: 100%;
  border-radius: 10px;
}

.overlay-text {
  position: absolute;
  width: 100%;
  top: 20%;
  left: 50%;
  transform: translate(-50%, -50%);
  /* background: rgba(0, 0, 0, 0.5); */
  /* color: white; */
  /* padding: 10px 20px; */
  /* border-radius: 5px; */
  font-size: 24px;
  font-weight: bold;

  color: black;
}

.countdown {
  font-size: 20px;
  margin: 20px 0;
  font-weight: bold;
}

.info {
  margin-top: 2em;
}

.info h2 {
  margin-bottom: 5px;
  text-decoration: hsl(var(--djH), 30%, 50%) 4px solid underline;
  text-underline-position: under;
}

.account {
  margin-top: 20px;
}

.account h2 {
  margin-bottom: 10px;
}

button {
  background: #007bff;
  color: white;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  border-radius: 5px;
}

button:hover {
  background: #0056b3;
}
